import { useCallback, useEffect, useState } from "react";
import { APIProvider, Map } from "@vis.gl/react-google-maps";
import { DeckGlOverlay } from "./deckgl-overlay";
import ControlPanel from "./control-panel";
import { GeoJsonLayer } from "@deck.gl/layers/typed";
import type { Feature, GeoJSON } from "geojson";

const RIDES_DATA_URL = "https://raw.githubusercontent.com/spudtrooper/spudtrooper.github.io/main/citibikeridesdata/rides-geojson.json";
const STATIONS_DATA_URL = "https://raw.githubusercontent.com/spudtrooper/spudtrooper.github.io/main/citibikeridesdata/stations-geojson.json";

const App = () => {
  const [stationsData, setStationsData] = useState<GeoJSON | null>(null);
  const [ridesData, setRidesData] = useState<GeoJSON | null>(null);
  const [showControlPanel, setShowControlPanel] = useState(true);

  useEffect(() => {
    const load = async () => {
      await Promise.all([
        fetch(RIDES_DATA_URL)
          .then(res => res.json())
          .then(data => setRidesData(data as GeoJSON)),
        fetch(STATIONS_DATA_URL)
          .then(res => res.json())
          .then(data => setStationsData(data as GeoJSON)),
      ]);
    };
    load();
  }, []);

  const getPathColor = (n: number): [number, number, number, number] => {
    const b = Math.floor(255 * (1 - n));
    const g = Math.floor(255 * n);
    const a = 255 - b;
    return [b, g, b, a];
  }

  const getDeckGlLayers = useCallback(() => [
    stationsData && new GeoJsonLayer({
      id: "stations-geojson-layer",
      data: stationsData,
      stroked: false,
      filled: true,
      extruded: true,
      pointType: "circle",
      getFillColor: [160, 160, 180, 200],
      getLineColor: [255, 0, 0, 255],
      getPointRadius: 50,
      getDataComparator: false,
      getElevation: 0,
    }),
    ridesData && new GeoJsonLayer({
      id: "rides-geojson-layer",
      data: ridesData,
      stroked: false,
      filled: true,
      extruded: true,
      lineWidthScale: 20,
      lineWidthMinPixels: 4,
      getLineColor: (f: Feature) =>
        getPathColor(f?.properties?.normalizedRideCount),
      getDataComparator: false,
      getElevation: 0,
    }),
  ].filter(Boolean),
    [stationsData, ridesData]);

  return (
    <APIProvider apiKey={""}>
      <Map
        defaultCenter={{ lat: 40.76696, lng: -73.98179 }}
        defaultZoom={12}
        mapId={"4f6dde3310be51d7"}
        gestureHandling={"greedy"}
        disableDefaultUI={true}>
        <DeckGlOverlay layers={getDeckGlLayers()} />
        {showControlPanel &&
          <ControlPanel hide={() => setShowControlPanel(false)} />}
      </Map>
    </APIProvider>
  );
};

export default App;