import * as React from "react";

interface ControlPanelProps {
  hide: () => void;
}

function ControlPanel(props: ControlPanelProps) {
  const { hide } = props;

  return (
    <div className="control-panel">
      <div style={{ position: "absolute", right: 10, top: 10 }}>
        <button onClick={() => hide()}>X</button>
      </div>
      <h3>citibike rides</h3>
      <p>
        A rough look at relative citibike traffic over the last week of March 2024.
      </p>

      <p>
        Data is from public APIs from <a href="https://citibikenyc.com/">citibikenyc.com</a>
        and visualization blatantly copied from
        <a href="https://visgl.github.io/react-google-maps/examples/deckgl-overlay">visgl.github.io</a>.
      </p>
    </div>
  );
}

export default React.memo(ControlPanel);
